import React from 'react'


const Buton = ({color, text, textcolor, onAbout, onClick}) => {
    return (
        <button 
        className="nav-button"
        onClick={onClick}
        style={{
        backgroundColor: color,
        color: textcolor,
        cursor: 'pointer',
        }}>
        {text}</button>
    )
}
Buton.defaultProps = {
    color: 'black',
    text: 'test',
    textcolor: 'white',

}
export default Buton
