import React from 'react';
import Button from './Comp/Buton';
import AboutMe from './Comp/Aboutme'
import Work from './Comp/Work'
import { useState } from 'react';
import './app.css';
import Logo from'./margi.PNG'



function App() {
  const [showThings, setshowThings] = useState(false)
  const [workthing, setWorkthing] = useState(false)
  return (
    <div className="Main">
      <div className="hero">
        <img className="hero-image" src={Logo} alt="" />
        <h1 className="hero-label">Mantas Aleknaviƒçius</h1>
      </div>
      <div className="navcon">
        <Button color="black" text="About me" 
        onClick={() => setshowThings
        (!showThings)}
        onAbout={showThings}/>
        <Button color="black" text="Work"
        onClick={() => setWorkthing
        (!workthing)}
        onWork={workthing}
        />
      </div>
      <div className="Click-show">
        {showThings && <AboutMe/>}
        {workthing && <Work/>}
      </div>
    </div>
  )
}

export default App;
